import React , {Component, PropTypes} from 'react';
import $ from 'jquery';
import Footer from '../../../components/footer/index.jsx';
import Header from '../../../components/header/index.jsx';

class Home extends Component {
    constructor(props) {
        super(props);
    }
    componentWillMount() {
        this.props.onLoading();
    }
    componentDidMount() {
        $('.content-for-m-header').attr('ui-content-for', 'm-header')
    }


    setSessionStorage(key, value){
        if(key === undefined || key === null) return;
        try{
            if(value === undefined || value === null) {
                sessionStorage.removeItem(key);
            }else{
                value = (typeof value === 'string' || typeof value === 'number') ? value : JSON.stringify(value);
                sessionStorage.setItem(key, value);
            }
        } catch (oException) {
            if (oException.name == 'QuotaExceededError') {
                alert('');
                sessionStorage.clear();
                sessionStorage.setItem(key, value);
            }
        }
    }

    // 
    // dialog(){
    //     var modalInstance = $modal.open({
    //         animation: true,
    //         templateUrl: 'xlbDialog',
    //         controller:'homePop',
    //         size: 20,
    //         resolve: {
    //             items: function () {
    //                 return $scope.items;
    //             }
    //         }
    //     });
    //     modalInstance.result.then(function (selectedItem) {
    //         $scope.selected = selectedItem;
    //     }, function () {
    //         $log.info('Modal dismissed at:' + new Date());
    //     });
    // };

    // 
    getInvestDetail (borrowId,pname,ifnew,limitTime) {
        if (arguments.length >= 4 && arguments[3] > 0) {
            // dialog();
            return; //
        }
        if(pname){
            this.setSessionStorage('Detail_tender_plan_name',pname)
        }
        if(ifnew==0 || ifnew==1){
            this.setSessionStorage('Detail_tender_if_new',ifnew+'');
        }
        this.setSessionStorage('Detail_borrowId', borrowId);
        window.location.href = "/#/invest/detail";
    };

    // 
    getTenderInfoDetail(borrowId) {
        if (arguments.length >= 2 && arguments[1] > 0) return;
        if (borrowId) {
            this.setSessionStorage('Detail_borrowId', borrowId);
        }
        window.location.href = "/#/invest";
    };

    gotoList(){
        window.location.href = "/#/invest/list" ;
    }
    render(){
        return (
            <div className="scrollable">
            <div className="scrollable-content k_p0">
                <div className="content-for-m-header">
                    <Header/>
                </div>
                <article className="wx-mainbody">   
                    {
                        this.props.banner.items ? 
                        this.props.banner.items.map( (item, i) => (
                            <div key={i} className="wx-banner">
                                <div className="index-banner">
                                    <a href={item.actionUrl}>
                                        <img src={item.imageUrl} />
                                    </a>
                                </div>
                            </div>
                        )) 
                        : "" 
                    }
                    <div className="wx-index-pro-cont">
                        <ul className="wx-index-pro-list" >
                            {
                                this.props.investList.investsList ?
                                this.props.investList.investsList.map( (list, idx) =>{
                                    let itemTitle;
                                    let fullThreshold = (list.isFullThreshold==1)?<span className="red-tag"></span>:"";
                                    if(list.isLimit != 1 || list.limitTime<=0){
                                        itemTitle = <h3><em className="h3-title">{fullThreshold}{list.name}</em><i></i></h3>;
                                    }else{
                                        itemTitle = <h3 className="count-down"><b></b><time>{list.timer}</time></h3>;
                                    }
                                    let isRead = (list.isReward==1)? <span className="take">{list.promotionTitle}</span> : "";
                                    let isBonusTicket = (list.isBonusticket == 1) ? <span ></span> : "";
                                    let isAllowIncrease = (list.isAllowIncrease == 1) ? <span></span> : "";
                                    let isTag = (list.isTag == 1) ? <span>{list.tagTitle}</span> : "";
                                    let recommendMark = (list.is_zhiding == true) ? <img src="./images/icon-recommend.png" className="recommend-mark"/> : "";
                                    return (
                                        <li key={idx}>
                                            <div className={ "pro-box " + list.biao_type_zi_bgcss} onClick={this.getInvestDetail.bind(this,list.id,false,list.isNew, list.limitTime)}>
                                                {itemTitle}
                                                <dl className="pro-info">
                                                    <dt>
                                                        <strong className={(list.isReward==0 && list.isBonusticket==0 && list.isAllowIncrease == 0 && list.isBonusticket==0)?'only-data':""}>
                                                            {list.annualRate}<b className="font-12">%</b>
                                                            {
                                                                (list.increaseApr > 0)? <b className="font-22">+{list.increaseApr}</b> : ""
                                                            }
                                                            {
                                                                (list.increaseApr > 0)? <b className="font-12">%</b>: ""
                                                            }
                                                        </strong>
                                                        {isRead}
                                                        {isBonusTicket}
                                                        {isAllowIncrease}
                                                        {isTag}
                                                    </dt>
                                                    <dd><span><b></b>{list.investmentHorizon}</span><span><b></b>{list.availableAmount} </span></dd>
                                                </dl>
                                            </div>
                                            <div className={"index-list-tag "+ list.biaotag}>{list.biao_type_zi}</div>
                                            {recommendMark}
                                        </li>
                                    )
                                })
                                : ""
                            }
                        </ul>
                        {
                            this.props.investList.investsLen === 1 ?
                            <div className="btn-area-com margin-t-1-5rem">
                                {
                                    (this.props.investList.investsLen === 1 && this.props.investList.investsList[0].statusMessage=='' && this.props.investList.investsList[0].biao_type_zi != '')
                                    ? <a className="orange-radius-btn wd-80" onClick={this.getTenderInfoDetail.bind(this, this.props.investList.investsList[0].id)}></a>
                                    : ""
                                }
                                {
                                    (this.props.investList.investsLen === 1 && this.props.investList.investsList[0].isLimit &&  this.props.investList.investsList[0].limitTime > 0)
                                    ? <a class="gray-radius-btn wd-80"></a>
                                    : ""
                                }
                                {
                                    (this.props.investList.lastTenderInfo !== false)
                                    ? <a class="gray-radius-btn wd-80">
                                    {
                                        new Date(this.props.investList.lastTenderInfo.addtime * 1000).getMonth()+1  + "" 
                                        +new Date(this.props.investList.lastTenderInfo.addtime * 1000).getDate() +""
                                        +new Date(this.props.investList.lastTenderInfo.addtime * 1000).getHours()+":"
                                        +new Date(this.props.investList.lastTenderInfo.addtime * 1000).getMinutes()+" "
                                    } 
                                    </a>
                                    : ""
                                }
                            </div>
                            :""
                        }
                        <div className="loading-btn" onClick={this.gotoList.bind(this)}></div>
                    </div>
                </article>
                <Footer />
            </div>
            </div>
        );
    }
}

Home.propTypes = {
    env: PropTypes.object.isRequired,
    onLoading: PropTypes.func.isRequired,
}

export default Home;


